/** *****
 * @description:
 * @param {*}
 * @return {*}
 */
/* eslint-disable */
const getElementY = elem => {
  return (
    window.pageYOffset +
    document.querySelector(elem).getBoundingClientRect().top
  )
}

/** *****
 * @description: ÊªöÂä®Âà∞ÊåáÂÆöÁöÑÂÖÉÁ¥†
 * @param {String} elem DOMÂÖÉÁ¥†
 * @param {Number} durction ÊªöÂä®Âä®ÁîªÊâßË°åÊó∂Èó¥
 * @param {Number} offset ÂÅèÁßªÈáè
 * @return {*}
 */
const ScrollToElem = (elem, durction, offset) => {
  // ÂàùÂßã‰ΩçÁΩÆ
  const startingY = window.pageYOffset
  const elementY = getElementY(elem)
  // ÈúÄË¶ÅÊªöÂä®ÁöÑË∑ùÁ¶ª
  const diff = elementY - startingY + offset
  if (!diff) return
  // ÊªëÂä®ÊïàÊûúÂáΩÊï∞
  const easing = t =>
    t < 0.5 ? 4 * t * t * t : (t - 1) * (2 * t - 2) * (2 * t - 2) + 1
  // ÂàùÂßãÂåñÊªöÂä®Êó∂Èó¥
  let start
  window.requestAnimationFrame(function step(timestamp) {
    if (!start) start = timestamp
    // ËÆ°ÁÆóÊó∂Èó¥ÁöÑÂ∑ÆÂÄºÔºåÊ†πÊçÆÂ∑ÆÂÄºËÆ°ÁÆóÂÅèÁßªÈáè
    const time = timestamp - start
    let percent = Math.min(time / durction, 1)
    percent = easing(percent)
    window.scrollTo(0, startingY + diff * percent)
    if (time < durction) {
      window.requestAnimationFrame(step)
    }
  })
}

export { getElementY, ScrollToElem }
